import React, { useState } from 'react';
import { Button, Modal, Row } from 'react-bootstrap';
import ReactTooltip from 'react-tooltip';
interface Props {
    open: boolean,
    close: () => void,
    codigo: string,
    title: string
}
export default function ModalPrint({ title, close, codigo, open }: Props) {
    const [precio, setPrecio] = useState(true);
    return <Modal show={open} onHide={close} size='xl'>
        <Modal.Header closeButton>
            <h6 className="m-0 font-weight-bold text-primary mr-auto align-self-center">
                <span><b>{title}</b></span>
            </h6>
        </Modal.Header>
        <Modal.Body>
            <Row>
                <ReactTooltip />
                <Button style={{ marginRight: "15px" }} className="btn-block" onClick={() => setPrecio(!precio)}
                    data-tip={precio ? "CAMBIAR A CON PRECIO" : "CAMBIAR A SIN PRECIO"} variant={precio ? "success" : "primary"}>
                    <span className="fa fa-print" />{precio ? "VISTA CON PRECIO" : "VISTA SIN PRECIO"}
                </Button>
                <iframe src={"/api/Nota/" + (precio ? 'PDF' : 'PDFSP') + "/" + codigo}
                    width='100%' height='700wh' title="print" scrolling='no' frameBorder='0' />
            </Row>
        </Modal.Body>
    </Modal>
}