import React from 'react'
import { Button, Col } from 'react-bootstrap';
import { Detalles, GeneralFila } from '../../../Models/General';
import AuxiliarNuevo from '../../../Utiles/Prototipos/AuxiliarNuevo'
import BuscarOrden from '../../Orden/BuscarOrden';
import FilaProducto from './FilaProducto';


export default class AuxiliarCompra extends AuxiliarNuevo<{ ordenes: 'COPIAR' | 'CARGAR', cargar: (d: GeneralFila) => void }> {
    nuevo = true;
    BotonesLeft() {
        const { ordenes, cargar } = this.props;
        return <Col xs={6}>
            <BuscarOrden open={this.state.aux} add={cargar} close={() => this.setState({ aux: false })} />
            <Button onClick={() => { this.setState({ aux: true }) }}
                data-class="toolbody" data-html="true"
                data-tip={ordenes + " ORDEN <br> DE COMPRA"} variant="primary">
                <span className="fa fa-folder-open" />
            </Button>
        </Col>
    }
    Row({ data }: { data: Detalles }) {
        return <FilaProducto key={'USD' + data.codigo} producto={data} edit={this.addProductos.bind(this)} remove={this.removeProductos.bind(this)} />;
    }
}