import React from "react";
import { Accordion, Button, Card, Col, InputGroup } from "react-bootstrap";
import { toast } from "react-toastify";
import { conCotizacion, conFactura, conNota, Cotizacion, Factura, Nota, sentFactura, sentNota, FacturaNull, updatePrecio } from "../../../Models/Factura";
import { GeneralFila } from "../../../Models/General";
import ClienteSelector from "../../Clientes/Selector";
import AuxiliarFactura from "./AuxiliarFactura";
import { InputArea } from "../../../Utiles/Inputs";
import { VentasContext, VentasType } from "../../../Context/Ventas";
import CreateOperacion from "../../../Utiles/Componentes/CreateOperacion";
class NuevaFactura extends CreateOperacion {
    con = conFactura();
    tipo = 'Factura'
    title = 'FACTURA'
    msn = 'LA FACTURA';
    constructor(p: any) {
        super(p);
        this.state = { ...FacturaNull, procesar: false };
    }
    setCliente(p: string) {
        this.setState({ cod_cliente: p });
    }
    Top() {
        const data = this.getData() as Factura;
        return <>
            <ClienteSelector Cliente={data.cod_cliente} add={this.setCliente.bind(this)} />
            <Accordion>
                <Col xs={5}>
                    <InputGroup style={{ width: '100%' }} className="mb-3">
                        <InputGroup.Prepend><InputGroup.Text>CONDICION</InputGroup.Text></InputGroup.Prepend>
                        <select onChange={this.setCondicion.bind(this)}
                            className="form-control"
                            defaultValue={data.condicion}>
                            <option value="CONTADO">CONTADO</option>
                            <option value="CREDITO">CREDITO</option>
                        </select>
                    </InputGroup>
                </Col>
                <Accordion.Toggle eventKey='0' as={Button} variant='primary'>OBSERVACI√ìN</Accordion.Toggle>
                <Accordion.Collapse eventKey='0'><Card>
                    <Card.Body>
                        <InputArea rows={4} maxLength={600} value={data.nota} setData={this.setState} update="nota" />
                    </Card.Body>
                </Card>
                </Accordion.Collapse>
            </Accordion>
        </>
    }
    Productos() {
        return <AuxiliarFactura key="auxFactura" setData={this.setState} data={this.state}
            setNota={this.cargarNota} setCotizacion={this.cargarCotizacion.bind(this)} />
    }
    Procesar() {
        let procesar = true;
        if (this.state.cod_cliente === "") {
            toast('DEBE SELECCIONAR UN CLIENTE', { type: toast.TYPE.ERROR });
            procesar = false;
        }
        procesar = this.validacionExixtencia(procesar);
        procesar = this.valdacionPrecio(procesar);
        procesar = this.validacionGenerica(procesar);
        if (procesar) {

            this.con.nuevo(sentFactura(this.getData(), this.context.impuesto), data => this.setState({ codigo: data }));
        }
    }
    cargarCotizacion({ codigo, rif }: GeneralFila) {
        const { tasa, dolar } = this.context as VentasType;
        this.setState({ cod_cliente: rif, id_cotizacion: codigo });
        conCotizacion().detalle(codigo, (data: Cotizacion) => this.setState(updatePrecio(data, tasa, dolar)));
    }
    cargarNota(d: GeneralFila) {
        conNota().detalle(d.codigo, (data: Nota) => {
            let p: any = sentNota(data, 0);
            p.detalles = data.detalles;
            p.id_nota = data.codigo;
            p.codigo = 0;
            if (this.state.nota !== '') p.nota = this.state.nota;
            this.setState(p)
        });
    }
    setCondicion(v: React.ChangeEvent<HTMLSelectElement>) {
        let value = v.currentTarget.value as string
        this.props.change(value)
    }
}
NuevaFactura.contextType = VentasContext;
export default NuevaFactura;