import React from 'react'
import { Col, Button, ButtonGroup } from 'react-bootstrap'
import { Detalles, GeneralFila } from '../../../Models/General'
import FilaProducto from './FilaProducto'
import AuxiliarNuevo, { ProductosModal } from '../../../Utiles/Prototipos/AuxiliarNuevo';
import { Factura } from '../../../Models/Factura'
import { toast } from 'react-toastify'
import { Producto } from '../../../Models/Productos'
import CalcularImpuesto from '../../../Utiles/Prototipos/CalcularImpuesto'
import { numberFormat } from '../../../Utiles/Formatos';
import BuscarCotizacion from '../../Cotizacion/BuscarCotizacion';
import BuscarNota from '../../Nota/BuscarNota';
export default class AuxiliarFactura extends AuxiliarNuevo<{ nota?: boolean, setNota: (d: GeneralFila) => void, setCotizacion: (d: GeneralFila) => void }> {
    productos: ProductosModal = 'Factura';
    addProductos(pr: Detalles) {
        let data = this.props.data as Factura;
        if (data.id_nota !== undefined)
            toast("UNA NOTA DE ENTREGA NO PUEDE SER EDITADA AL FACTURARLA", { type: toast.TYPE.ERROR })
        else super.addProductos(pr)
    }
    removeProductos(pr: Detalles) {
        let data = this.props.data as Factura;
        if (data.id_nota !== undefined)
            toast("UNA NOTA DE ENTREGA NO PUEDE SER EDITADA AL FACTURARLA", { type: toast.TYPE.ERROR })
        else super.removeProductos(pr)
    }
    createProducto(p: Producto) {
        let data = this.props.data as Factura;
        if (data.id_nota !== undefined)
            toast("UNA NOTA DE ENTREGA NO PUEDE SER EDITADA AL FACTURARLA", { type: toast.TYPE.ERROR })
        else super.createProducto(p)
    }
    BotonesLeft() {
        const { props: { setNota, setCotizacion }, state: { aux2, aux } } = this;
        return <Col xs={6}>
            <BuscarCotizacion open={aux} add={setCotizacion} close={() => this.setState({ aux: false })} />
            <BuscarNota open={aux2} add={setNota} close={() => this.setState({ aux2: false })} />
            <ButtonGroup>
                <Button onClick={() => this.setState({ aux: true })}
                    data-class="toolbody" data-html="true"
                    data-tip="CARGAR COTIZACION" variant="primary">
                    <span className="fa fa fa-calculator" />
                </Button>
                <Button onClick={() => this.setState({ aux2: true })}
                    data-class="toolbody" data-html="true"
                    data-tip="CARGAR NOTA" variant="primary">
                    <span className="fa fa-paper-plane" />
                </Button>
            </ButtonGroup>
        </Col>
    }
    BotonesRight() {
        const { setData } = this.props;
        let { dolar } = this.props.data;
        return <Col xs={6} style={{ textAlign: 'right' }}>
            <Button data-tip="PROCESAR" style={{ marginRight: "15px" }} variant="success"
                className="float-right" onClick={() => setData({ procesar: true })} >
                <span className="fa fa-print" />
            </Button>
            <ButtonGroup className="float-right" style={{ marginRight: "15px" }}>
                {this.createProductoValidation() ?
                    <Button data-tip="NUEVO PRODUCTO" onClick={() => { this.setState({ nuevoProducto: true }) }} variant="info">
                        <i className="fa fa-edit"></i>
                    </Button> : <></>}
                <Button data-tip="BUSCAR PRODUCTO" onClick={() => { this.setState({ buscarProducto: true }) }} variant="primary">
                    <span className="fa fa-search"></span>
                </Button>
            </ButtonGroup>
        </Col >
    }
    Row({ data }: { data: Detalles }) {
        return <FilaProducto nota={this.props.data.id_nota} key={'USD' + data.codigo} producto={data} edit={this.addProductos.bind(this)} remove={this.removeProductos.bind(this)} />;
    }

    DolaresChange() {
        const { setData, nota } = this.props;
        let { dolar } = this.props.data as Factura;
        if (nota) return <></>;
        return dolar ? <Button data-tip="CAMBIAR A BOLIVARES" style={{ marginRight: "15px" }}
            variant="warning" onClick={() => setData({ dolar: !dolar })} >
            <span className="fa fa-usd" /> COTIZACION ESTA EN DOLARES
        </Button> :
            <Button data-tip="CAMBIAR A DOLARES" style={{ marginRight: "15px" }}
                variant="primary" onClick={() => setData({ dolar: !dolar })} >
                <b>BS</b> COTIZACION ESTA EN BOLIVARES
            </Button>
    }

    THead() {
        return <tr className='info'>
            <th style={{ width: "90px", textAlign: 'center' }}>CODIGO</th>
            <th style={{ textAlign: 'center' }}>DESCRIPCI√ìN</th>
            <th style={{ width: "120px", textAlign: 'center' }}>CANTIDAD</th>
            <th style={{ width: "120px", textAlign: 'center' }}>DISPONIBLE</th>
            <th style={{ width: "180px", textAlign: 'center' }}>P.U.</th>
            <th style={{ width: "230px", textAlign: 'center' }}>TOTAL</th>
            <th style={{ width: "110px", textAlign: 'center' }}>ACCIONES</th>
        </tr>
    }
    Calcular({ imponible, exento }: { imponible: number, exento: number }) {
        if (this.props.nota)
            return <tr>
                <th colSpan={3} rowSpan={8}></th>
                <th style={{ textAlign: "right" }} colSpan={2}>TOTAL:</th>
                <th style={{ textAlign: "right" }}>{numberFormat(imponible + exento)}</th>
            </tr>
        return <CalcularImpuesto exento={exento} imponible={imponible} />;
    }
}