import React, { useState } from 'react';
import { Button, Modal, Row } from 'react-bootstrap';
import ReactTooltip from 'react-tooltip';
interface Props {
    open: boolean,
    close: () => void,
    codigo: string,
    title: string
}
export default function ModalPrint({ title, close, codigo, open }: Props) {
    const [dolar, setDolar] = useState(false);
    return <Modal show={open} onHide={close} size='xl'>
        <Modal.Header closeButton>
            <h6 className="m-0 font-weight-bold text-primary mr-auto align-self-center">
                <span><b>{title}</b></span>
            </h6>
        </Modal.Header>
        <Modal.Body>
            <Row>
                <ReactTooltip />
                <Button style={{ marginRight: "15px" }} className="btn-block" onClick={() => setDolar(!dolar)}
                    data-tip={dolar ? "CAMBIAR A BOLIVARES" : "CAMBIAR A DOLARES"} variant={dolar ? "warning" : "primary"}>
                    {dolar ? <><span className="fa fa-usd" />VISTA EN DOLARES</> : <><b>BS</b> VISTA EN BOLIVARES</>}
                </Button>
                <iframe src={"/api/Cotizacion/" + (dolar ? 'PDFD' : 'PDF') + "/" + codigo}
                    width='100%' height='700wh' title="print" scrolling='no' frameBorder='0' />
            </Row>
        </Modal.Body>
    </Modal>
}