import React from "react";
import { Accordion, Button, Card } from "react-bootstrap";
import { toast } from "react-toastify";
import ReactTooltip from "react-tooltip";
import { VentasContext, VentasType } from "../../Context/Ventas";
import { conCotizacion, conNota, Cotizacion, Nota, sentNota, updatePrecio } from "../../Models/Factura";
import { GeneralFila, generalNull } from "../../Models/General";
import CreateOperacion from "../../Utiles/Componentes/CreateOperacion";
import { InputArea } from "../../Utiles/Inputs";
import ClienteSelector from "../Clientes/Selector";
import AuxiliarFactura from "../Facturas/Nuevo/AuxiliarFactura";
class NuevaNota extends CreateOperacion {
    tipo = 'Nota';
    title = 'NOTA DE ENTREGA';
    msn = 'LA NOTA DE ENTREGA';
    con = conNota();
    constructor(p: any) {
        super(p);
        this.state = { ...generalNull, cod_cliente: '', procesar: false };
    }
    setCliente(p: string) {
        this.setState({ cod_cliente: p });
    }
    Top() {
        const { cod_cliente, nota } = this.getData() as Nota;
        return <>
            <ClienteSelector Cliente={cod_cliente} add={this.setCliente.bind(this)} />
            <Accordion>
                <Accordion.Toggle eventKey='0' as={Button} variant='primary'>NOTA</Accordion.Toggle>
                <Accordion.Collapse eventKey='0'><Card>
                    <Card.Body>
                        <InputArea rows={4} maxLength={600} value={nota} setData={this.setState} update="nota" />
                    </Card.Body>
                </Card>
                </Accordion.Collapse>
            </Accordion>
        </>
    }

    PDF() {
        const { procesar, codigo } = this.state;
        return <Card id="tabla">
            <ReactTooltip />
            <Button style={{ marginRight: "15px" }} className="btn-block" onClick={() => this.setState({ procesar: !procesar })}
                data-tip={!procesar ? "CAMBIAR A CON PRECIO" : "CAMBIAR A SIN PRECIO"} variant={!procesar ? "success" : "primary"}>
                <span className="fa fa-print" />{!procesar ? "VISTA CON PRECIO" : "VISTA SIN PRECIO"}
            </Button>
            <iframe src={"/api/Nota/" + (!procesar ? 'PDF' : 'PDFSP') + "/" + codigo}
                width='100%' height='700wh' title="print" scrolling='no' frameBorder='0' />
        </Card>;
    }
    Productos() {
        return <AuxiliarFactura key="auxFactura" setData={this.setState} data={this.state} nota
            setNota={this.cargarNota} setCotizacion={this.cargarCotizacion.bind(this)} />
    }
    Procesar() {
        let procesar = true;
        let generico = this.validacionGenerica();
        let cantidad = this.validacionExixtencia();
        let precio = this.valdacionPrecio();
        if (!(generico && cantidad && precio)) procesar = false;
        if (this.state.cod_cliente === "") {
            toast('DEBE SELECCIONAR UN CLIENTE', { type: toast.TYPE.ERROR });
            procesar = false;
        }
        generico = this.validacionGenerica(procesar);
        cantidad = this.validacionExixtencia(procesar);
        precio = this.valdacionPrecio(procesar);
        if (procesar)
            this.con.nuevo(sentNota(this.getData(), this.context.impuesto), data => this.setState({ codigo: data }));
    }
    cargarCotizacion({ codigo, rif }: GeneralFila) {
        const { tasa, dolar } = this.context as VentasType;
        this.setState({ id_cotizacion: codigo, cod_cliente: rif });
        conCotizacion().detalle(codigo, (data: Cotizacion) => this.setState(updatePrecio(data, tasa, dolar)));
    }
    cargarNota(d: GeneralFila) {
        this.con.detalle(d.codigo, (data) => {
            data.codigo = 0;
            data.nota = this.state.nota;
            this.setState(data)
        });
    }
}
NuevaNota.contextType = VentasContext;
export default NuevaNota;